	module.exports = {"data":{"site":{"siteMetadata":{"title":"メモログ","author":"Yutaka Yamaguchi"}},"markdownRemark":{"id":"/Users/yyamaguchi/Desktop/memolog/src/pages/initial-post/index.md absPath of file >>> MarkdownRemark","html":"<p>更新しなくなったブログをリニューアルしようと思って、\b<a href=\"https://www.gatsbyjs.org\">Gatsby</a> で構築しようと思い立って、今作成してみている。</p>\n<p>\bGatsbyは「Blazing-fast static site generator for React」ということで、\bReactベースのスタティックサイトジェネレータらしいのだけど、実のところまだよくわかっていない。<a href=\"https://www.gatsbyjs.org/docs/\">Getting Started</a> のドキュメントをみて、\b<a href=\"https://github.com/gatsbyjs/gatsby-starter-blog\">gatsby-starter-blog</a> のstarterをGithub pages で公開しているだけなのだ。</p>\n<p>とはいえ、一応最初に行ったことをそのまま書き記しておこうと思う。</p>\n<h3>nvmで Node 9.xをインストール</h3>\n<p>インストール時か\b<code>gatsby develop</code>のときか失念してしまったが、どこかのタイミングで<code>error serve@6.4.3: The engine \"node\" is incompatible with this module. Expected version \">=6.9.0\".</code>というエラーが発生してしまったので、ローカル環境に9.2をインストール（今確認したらすでに最新は9.3だったが）。<a href=\"https://github.com/creationix/nvm\">nvm</a>はだいぶ前にインストールしたのでどうインストールしたか失念。<a href=\"https://brew.sh/\">Homebrew</a>\bでインストールしたのかもしれない。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>nvm <span class=\"token function\">install</span> 9\nnvm <span class=\"token function\">alias</span> default 9\n</code></pre>\n      </div>\n<p>デフォルトの設定を6.xにしていたのだけど、これを機にローカルで使用するバージョンは9.xに変更した。</p>\n<h3>Gatsby CLI のインストール とサイトの作成</h3>\n<p>ここから先は基本的には<a href=\"https://www.gatsbyjs.org/docs/\">Gatsby getting started</a>と同じことをただやった。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">npm</span> <span class=\"token function\">install</span> -g gatsby-cli\ngatsby new memolog https://github.com/gatsbyjs/gatsby-starter-blog\n</code></pre>\n      </div>\n<h3>Github Pages へのデプロイ</h3>\n<p>Github pagesにデプロイするためにレポジトリをGithub上で作成して、\bレポジトリを\b設定。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> init\n<span class=\"token function\">git</span> remote add origin git@github.com:memolog/site.git\n<span class=\"token function\">git</span> add <span class=\"token keyword\">.</span>\n<span class=\"token function\">git</span> commit\n<span class=\"token function\">git</span> push origin master\n</code></pre>\n      </div>\n<p>そのあと、 <a href=\"https://www.gatsbyjs.org/docs/deploy-gatsby/#github-pages\">Deploying Getsby</a>の内容にしたがって、\bGithub Pagesへのデプロイ準備\n<a href=\"https://yarnpkg.com/docs/install\">Yarn</a>はいつインストールしたか失念したが、<code>brew install yarn --without-node</code> でインストールしたと思う。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>yarn add gh-pages --dev\nyarn deploy\n</code></pre>\n      </div>\n<p>これで完了。いまのところデフォルトのテンプレートそのままなので、これからその辺りを更新していこうと思う。とりあえずプロフィール画像だけでも\b変更しておきたい。</p>\n<p>個人的にはカスタムドメインを使用したかっただけど、\bSSL証明書の関係や、Amazon S3とCloudFront、Route53で新しいドメイン\b用意しても、個人サイトのトラフィックだとたいしてお金かからないらしいのだけど、\b個人サイトだし、いつ気が変わってやめるかもわからないし、お金かけずに\bやる方がいいかなと\b思ってgithub.ioをそのまま使うことにした。</p>\n<p>（テスト投稿するつもりだけだったのに無駄に文字を書き連ねてしまった）</p>\n<hr>\n<p>ちなみにこの文章を書いているエディタはVisual Code Basicを使用しているが、以前にSlack Appでも問題があった<a href=\"https://github.com/electron/electron/issues/9173\">Electronで日本語入力でバックスペースすると文字化け\bする問題</a>が残っていて、少々辛い。<a href=\"https://github.com/Microsoft/vscode/blob/master/package.json\">\bVisual Code Base のソース</a>を見ると、Electron 1.7.0 を使用している雰囲気だが、1.7でもまだ問題が残っているということなのでしょう。現状のステータスが\bよくわからないけど、公開前に不要な文字を消すように何か対策を考えようと今は思っている（何もしないかもしれないけど）。</p>","frontmatter":{"title":"Gatsbyでスタティックサイトを作成する","date":"December 29, 2017"}}},"pathContext":{"slug":"/initial-post/"}}

